.nav{
    z-index: 1000;
    position: fixed;
    top: 0; left: 0;
    width: 100%;
    height: 30px;
    padding: 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    img{cursor: pointer;}
}
.nav__white{
    z-index: 1000;
    position: fixed;
    top: 0; left: 0;
    width: 100%;
    height: 30px;
    padding: 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: rgba(255, 255, 255, 0.7);
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    backdrop-filter: blur(2px);
}
.Nav_menu{
    width: 100px;
    font-size: 35px;
}